@import "variables";

.process {
  background: #f2f9ff;
  padding: 250px 0;

  &-inner {
    max-width: 1200px;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;

    .process-description {
      flex-basis: 30%;

      &__name {
        font-weight: bold;
        font-size: 13px;
        line-height: 19px;
        letter-spacing: 3px;
        text-transform: uppercase;
        color: #c0cdd7;
      }

      &__title {
        font-weight: 600;
        font-size: 32px;
        line-height: 42px;
        color: $title-color;
      }

      &__text {
        font-family: $font-family-secondary;
        font-size: 14px;
        line-height: 21px;
        color: #6c788b;
      }
    }

    .process-services {
      flex-basis: 60%;
      display: flex;
      justify-content: flex-end;
      align-items: center;
      flex-wrap: wrap;
    }

    .process-card {
      background: $base-color;
      border-radius: 24px;
      height: 264px;
      width: 264px;
      padding: 32px;
      margin: 0 30px 30px 0;

      &:nth-child(odd) {
        transform: translateY(60px);
      }
      &:nth-child(even) {
        transform: translateY(-50px);
      }
      &__count {
        border-radius: 23px;
        padding: 2px 12px;
        font-weight: 600;
        font-size: 13px;
        line-height: 19px;
      }

      &__list {
        padding: 24px 0 32px;
        margin: 0;
      }

      &__item {
        text-decoration: none;
        list-style-type: none;
        font-family: $font-family-secondary;
        font-weight: 600;
        font-size: 13px;
        line-height: 19px;
        color: #6c788b;
      }

      &.first,
      &.second,
      &.third,
      &.fourth {
        .process-sources {
          display: flex;
          justify-content: start;
          align-items: center;

          &__link {
            font-weight: 600;
            font-size: 15px;
            line-height: 22px;
            color: $title-color;
            text-decoration: none;
          }

          &__button {
            width: 40px;
            height: 40px;
            border-radius: 50%;
            border: none;
            outline: none;
            background: url("../images/arrow.png") no-repeat center;
            transition: 0.3s ease-in-out;
            margin-right: 16px;

            &:hover {
              transform: scale(1.2);
              transition: 0.3s ease-in-out;
            }
          }
        }
      }

      &.first {
        .process-card__count {
          color: #56ccf2;
          background: #edfaff;
        }

        .process-sources {
          &__button {
            background-color: #008aff;
          }
        }
      }

      &.second {
        .process-card__count {
          color: #f0c940;
          background: #fffaea;
        }

        .process-sources {
          &__button {
            background-color: #fcc91d;
          }
        }
      }

      &.third {
        .process-card {
          z-index: 1;
        }

        .process-card__count {
          color: #3bc063;
          background: #e5feeb;
        }

        .process-sources {
          &__button {
            background-color: #15d850;
          }
        }
      }

      &.fourth {
        .process-card__count {
          color: #f36d6c;
          background: #fef0f0;
        }

        .process-sources {
          &__button {
            background-color: #f36363;
          }
        }
      }
    }
  }
}
@media (max-width: 1220px) {
  .process-inner {
    width: 100%;
    //margin: 0 auto;
  }
}

@media (max-width: 800px) {
  .process-inner {
    width: 100%;
    flex-direction: column;
    .process-description {
      text-align: center;
    }
    .process-card {
      margin: 0 50px;
      &:nth-child(odd) {
        transform: translateY(0);
      }
    }
    .process-services {
      justify-content: center;
    }
  }
}
